<template>
  <div class="query-panel">
    <c-table class="query-talbe" v-model="model" :dsl="dsl">
      <tr slot="submit">
        <td colspan="4" class="query-button-wrapper">
          <el-button @click="query" type="primary" class="button-query">查询</el-button>
        </td>
      </tr>
    </c-table>
    <el-table
      class="query-list"
      :data="tableModel"
      @cell-dblclick="handleDblclickCell"
      @row-dblclick="handleDblclickRow"
      height="250"
    >
      <template v-for="(tableH, index) in tableHeader">
        <el-table-column
          v-if="tableH.prop==='jcdh'"
          :prop="tableH.prop"
          :label="tableH.label"
          :key="index"
        >
          <template slot-scope="scope">
            <el-form :model="scope.row" ref="dynamicValidateForm" class="demo-dynamic">
              <el-form-item prop="jcdh" :rules="rules.jcdh">
                <el-input v-model="scope.row.jcdh"></el-input>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <!-- 检查结果 -->
        <el-table-column v-else :prop="tableH.prop" :label="tableH.label" :key="index"></el-table-column>
      </template>
    </el-table>
    <!-- pagepagation -->
  </div>
</template>

<script>
export default {
  name: "QueryPanel",
  model: {
    prop: "model",
    event: "input"
  },
  props: {
    model: { required: true },
    dsl: { required: true },
    tableInfo: { required: true }
  },
  mounted() {},
  computed: {
    tableHeader() {
      return this.tableInfo.tableHeader;
    },
    tableModel() {
      return this.tableInfo.tableModel;
    }
  },
  data() {
    return {};
  },
  methods: {
    query() {
      this.$emit("queryClick");
    },
    handleDblclickCell(row, column, cell, event) {
      console.log(row, column, cell, event);
      this.$emit("cell-dblclick", row, column, cell, event);
    },
    handleDblclickRow(row, event) {
      console.log(row, event);
      this.$emit("row-dblclick", row, event);
    }
  },
  watch: {},
  components: {
    CTable: () => import(/* webpackChunkName: "FormTable" */ "@c/FormTable.vue")
  }
};
</script>

