<template>
  <div class="table-from" v-if="model">
    <el-form
      class="form"
      :model="model"
      :rules="rules"
      ref="model"
      :label-position="'left'"
      :disabled="disabled"
    >
      <!-- {{console("FormTable:model",model)}} -->
      <table class="table" :cellspacing="cellspacing">
        <tr class="tr-form-table" v-for="(trS, trIndex) in dsl" :key="'tr'+trIndex">
          <template v-for="(tdS, tdIndex) in trS">
            <!-- input -->
            <td
              v-if="isType(tdS,TYPE_STRING_TD.INPUT)"
              class="td"
              :key="'td'+tdIndex"
              :rowspan="tdS.rowspan"
              :colspan="tdS.colspan"
              :width="tdS.width"
            >
              <c-item
                :item="tdS.items[0]"
                :model="model"
                :readonly="readonly"
                @eventAvatarUploadSucess="handleAvatarSuccess"
              ></c-item>
            </td>
            <td
              v-else-if="isType(tdS,TYPE_STRING_TD.LABEL1)"
              class="td label1"
              :key="'td'+tdIndex"
              :rowspan="tdS.rowspan"
              :colspan="tdS.colspan"
            >
              <el-col :span="11">
                <c-item :item="tdS.items[0]" :model="model" :readonly="readonly"></c-item>
              </el-col>
              <el-col class="line" :span="2">-</el-col>
              <el-col :span="11">
                <c-item :item="tdS.items[1]" :model="model" :readonly="readonly"></c-item>
              </el-col>
            </td>
            <td
              v-else-if="isType(tdS,TYPE_STRING_TD.LABEL2)"
              class="td label2"
              :key="'td'+tdIndex"
              :rowspan="tdS.rowspan"
              :colspan="tdS.colspan"
            >
              <div class="col-wrapper">
                <c-item :item="tdS.items[0]" :model="model" :readonly="readonly"></c-item>
              </div>
              <div class="stamp">&nbsp;</div>
              <div class="col-wrapper">
                <c-item :item="tdS.items[1]" :model="model" :readonly="readonly"></c-item>
              </div>
            </td>
            <td
              v-else-if="isType(tdS,TYPE_STRING_TD.LABEL3)"
              class="td label3"
              :key="'td'+tdIndex"
              :rowspan="tdS.rowspan"
              :colspan="tdS.colspan"
            >
              <el-col :span="11">
                <c-item :item="tdS.items[0]" :model="model" :readonly="readonly"></c-item>
              </el-col>
              <el-col class="line" :span="2">/</el-col>
              <el-col :span="11">
                <c-item :item="tdS.items[1]" :model="model" :readonly="readonly"></c-item>
              </el-col>
            </td>
            <!-- input end -->
            <!-- division -->
            <td
              v-else-if="isType(tdS,TYPE_STRING_TD.DIVISION)"
              class="td division"
              :key="'td'+tdIndex"
              :rowspan="tdS.rowspan"
              :colspan="tdS.colspan"
            ></td>
            <!-- division -->
          </template>
        </tr>
        <slot name="submit"></slot>
      </table>
    </el-form>
  </div>
</template>


<script>
// import { dsl, model, rules } from "./FormTable.mock.js";
import { TYPE_STRING_TD } from "./input_type_string.js";

export default {
  name: "FormTable",
  model: {
    prop: "model",
    event: "input"
  },
  props: {
    dsl: {
      type: Array,
      required: true
    },
    model: { /* type: Object, */ required: true },
    rules: { type: Object },
    tableInfo: Object /* cellspacing */,
    readonly: {
      type: Boolean,
      default() {
        return false;
      }
    },
    disabled: {
      type: Boolean,
      default() {
        return false;
      }
    }
  },
  mounted() {},
  data() {
    return { TYPE_STRING_TD };
  },
  methods: {
    /* 头像上传，回调返回 */
    handleAvatarSuccess(res, item) {
      this.$emit("eventAvatarUploadSucess", res, item);
    },
    isType(tdS, typeString) {
      if (tdS.items.length < 1) return false;
      return tdS.type === typeString;
    },
    valid() {
      return new Promise(resole => {
        this.$refs["model"].validate(valid => {
          resole(valid);
        });
      });
    },
    reset() {
      this.$refs["model"].resetFields();
    },
    console(...msg) {
      console.log(msg);
    }
  },
  computed: {
    cellspacing() {
      return (this.tableInfo && this.tableInfo.cellspacing) || "";
    }
  },
  components: {
    CItem: () =>
      import(/* webpackChunkName: "FormTableItem" */ "@c/FormTableItem.vue")
  }
};
</script>

