            // @include elevation2();

            .el-form-item__content {
              // outline: 1px solid red;
              flex: 1;
              > div {
                width: 100%;
              }
              .is-disabled {
                // outline: 2px solid rgb(0, 255, 85);
                input {
                  color: black;
                  background-color: #fff;
                }
                textarea {
                  color: black;
