            >
              <div :class="['item-content',{'nodata':!model[tdS.id]}]">{{model[tdS.id]||"无数据"}}</div>
            </td>
            <td
              v-else-if="tdS.type==='panel-title'"
              class="td panel-title"
              :key="'td'+tdIndex"
              :rowspan="tdS.rowspan"
              :colspan="tdS.colspan"
              :width="tdS.width"
            >
              <div class="title-wrapper">
                <div class="line"></div>
                <span>{{tdS.label}}</span>
                <div class="line"></div>
              </div>
            </td>
            <td
              v-else-if="tdS.type==='inner-table'"
              class="td panel-title"
              :key="'td'+tdIndex"
              :rowspan="tdS.rowspan"
              :colspan="tdS.colspan"
              :width="tdS.width"
            >
              <div class="table-wrapper">
                <el-table
                  :data="(APP.model&&APP.model[tdS.info.modelName])||modelMockArray"
                  style="width: 100%"
                  height="200"
                >
                  <el-table-column
                    :prop="item.prop"
                    :label="item.label"
                    v-for="(item, index) in tdS.info.dsl"
                    :key="index"
                    :width="item.width"
                  ></el-table-column>
                </el-table>
              </div>
            </td>
          </template>
        </tr>
        <slot name="submit"></slot>
      </table>
    </el-form>
  </div>
