<script>
// import { dsl, model, rules } from "./FormTable.mock.js";
import { TYPE_STRING_TD } from "./input_type_string.js";

export default {
  name: "FormTable",
  inject: ["APP"],
  model: {
    prop: "model",
    event: "input"
  },
  props: {
    dsl: {
      type: Array,
      required: true
    },
    model: { /* type: Object, */ required: true },
    rules: { type: Object },
    tableInfo: Object /* cellspacing */,
    readonly: {
      type: Boolean,
      default() {
        return false;
      }
    },
    disabled: {
      type: Boolean,
      default() {
        return false;
      }
    }
  },
  mounted() {},
  data() {
    return { TYPE_STRING_TD, modelMockArray: [] };
  },
  methods: {},
  computed: {
    cellspacing() {
      return (this.tableInfo && this.tableInfo.cellspacing) || "";
    }
  },
  components: {}
};
</script>

